COaT,gBPNW,COOX,eAAgB,CAChB,QAAS","sourcesContent":["html {\r\n    font-size: 62.5%;\r\n}\r\nbody {\r\n    font-family: Helvetica, Arial, sans-serif;\r\n    font-size: $m-size;\r\n    background: $back;\r\n    color: $one;\r\n}\r\n\r\n.button {\r\n    cursor: pointer;\r\n}\r\n\r\n.button:disabled {\r\n    cursor: default;\r\n}","// Colors\r\n$back: black;\r\n$one: #2d5785;\r\n$two: grey;\r\n$three: #172d45;\r\n$four: grey ;\r\n\r\n// Spacing\r\n$s-size: 1.2rem;\r\n$m-size: 1.6rem;\r\n$l-size: 3.2rem;\r\n$xl-size: 4.8rem;\r\n\r\n",".ReactModalPortal > div{\r\n    opacity:0;\r\n    border-radius: 10px;\r\n}\r\n\r\n.ReactModalPortal .ReactModal__Overlay {\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    transition: opacity 200ms ease-in-out;\r\n} \r\n\r\n.ReactModalPortal .ReactModal__Overlay--after-open {\r\n    opacity: 1;\r\n} \r\n\r\n.ReactModalPortal .ReactModal__Overlay--before-close {\r\n    opacity: 0;\r\n} \r\n\r\n.modal {\r\n    background: $back;\r\n    color: $one;\r\n    max-width: 30rem;\r\n    outline: none;\r\n    padding: $l-size;\r\n    text-align: center;\r\n    border-radius: 10px;\r\n\r\n}\r\n\r\n\r\n.modal__title {\r\n    margin: 0 0 $m-size 0;\r\n}\r\n\r\n.modal__body {\r\n    margin: 0 0 $m-size 0;\r\n    font-size: 2rem;\r\n    font-weight: 300;\r\n    word-break: break-all;\r\n}\r\n","// add option error \r\n.add-option-error {\r\n    color: $one;\r\n    font-style: italic;\r\n    padding: 0 $m-size;\r\n    margin-top: $m-size;\r\n}\r\n\r\n.add-option{\r\n    display: flex;\r\n    border-radius: 10px;\r\n    padding: $m-size;\r\n}\r\n\r\n.add-option__input {\r\n    flex-grow: 1;\r\n    border-radius: 10px;\r\n    border: none;\r\n    background: $back;\r\n    border-bottom: .3rem solid darken($back, 10%);\r\n    margin-right: $s-size;\r\n    color: $one;\r\n    padding: $s-size;\r\n\r\n}",".option{\r\n    display: flex;\r\n    justify-content: space-between;\r\n    padding: $m-size $l-size;\r\n    border-bottom: lighten($back, 10%);\r\n}\r\n\r\n.option__text{\r\n    margin: 0;\r\n    font-weight: 500;\r\n    font-size: 2rem;\r\n    word-break: break-all;\r\n}",".widget{\r\n    color: darken($one, 10%);\r\n    background: lighten($back, 7%);\r\n    margin-bottom: $xl-size;\r\n    border-radius: 10px;\r\n\r\n}\r\n\r\n.widget-header{\r\n    background: lighten($back, 5%);\r\n    color: $one;\r\n    padding: $s-size;\r\n    display: flex;\r\n    justify-content: space-between;\r\n    border-radius: 10px;\r\n}\r\n\r\n.widget-header__title {\r\n    margin: 0;\r\n}\r\n\r\n.widget--message {\r\n    text-align: center;\r\n    padding: $l-size;\r\n    font-weight: 400;\r\n    margin: 0;\r\n    border-bottom: 5px solid  lighten($back, 10%);\r\n    \r\n}\r\n","// big button\r\n.big-button {\r\n    background: $three;\r\n    border: none;\r\n    border-bottom: .6rem solid darken($three, 10%);\r\n    color: whitesmoke;\r\n    font-weight: bold;\r\n    font-size: $l-size;\r\n    width: 100%;\r\n    margin-bottom: $xl-size;\r\n    padding: 2.4rem;\r\n    border-radius: 10px;\r\n}\r\n\r\n.big-button:disabled {\r\n    opacity: 0.5;\r\n}\r\n\r\n.button {\r\n    background: $three;\r\n    border: none;\r\n    border-bottom: .3rem solid darken($three, 10%);\r\n    color: $four;\r\n    font-weight: 500;\r\n    padding: $s-size;\r\n    border-radius: 10px;\r\n\r\n}\r\n\r\n.button--link {\r\n    background: none;\r\n    border: none;\r\n    color: $three;\r\n    padding: 0;\r\n}\r\n\r\n// bem\r\n",".container {\r\n    max-width: 60rem;\r\n    margin: 0 auto;\r\n    padding: 0 $m-size;\r\n}\r\n",".header {\r\n    background: $back;\r\n    margin-bottom: $xl-size;\r\n    padding: $m-size, 0;\r\n    color: $one;\r\n}\r\n\r\n// BEM block element modifier naming convention\r\n.header__title {\r\n    font-size: $l-size;\r\n    margin: 0;\r\n}\r\n\r\n.header__subtitle {\r\n    color: $one;\r\n    font-size: $m-size;\r\n    font-weight: 500;\r\n    margin: 0;\r\n}"],"sourceRoot":""}]);
